// Shrink everything slightly in low res
html {
	@media #{breakpoint(tablet-max)} {
		font-size: 80%;
	}
}

// Make basically everything a container
#four-o-four,
nav.pagination,
#video,
#comments,
#post-comment,
#contact-form {
	@include container;
}

nav.pagination {
	text-align: center;
}

// Or a hero
#archive-header {
	@include hero;

	text-shadow: .1rem .1rem .1rem rgba(0, 0, 0, .2);
}

// Or cards in cols
#child-pages,
#featured-posts,
#latest-posts,
#posts,
#text-blocks,
#users {
	@include header-and-cards;
}
